// src/plugins/vuetify.js

import Vue from 'vue'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import colors from 'vuetify/lib/util/colors'
Vue.use(Vuetify)

const opts = {
  theme: {
    dark:false,
    font:true,
    themes: {
      light: {
        primary: colors.red.darken1,
        assist_primary:colors.amber.darken2,
        background:"#ffffff",
        anti_background:colors.grey.darken4,
        light_text:"#000000",
        sex_color:colors.pink.darken2,
        fight_color:colors.orange.darken4,
        disgusting_color:colors.deepOrange.darken3,
        politics_color:colors.lightGreen.darken3


      },
      dark:{
        primary:colors.blue.darken2,
        background:colors.grey.darken4,
        assist_primary:colors.indigo.darken3,
        anti_background:"#ffffff",
        light_text:"#ffffff"

      }
    },
  },
}

export default new Vuetify(opts)



