import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/',
    name: 'welcome',
    meta: { index: 0 },
    component: () => import('@/view/welcome'),
  },
  {
    path: '/login',
    name: 'login',
    meta: { index: 0 },
    component: () => import('@/view/login'),
  }, {
    path: '/edit',
    name: 'edit',
    meta: { index: 1 },
    component: () => import('@/view/edit')
  }, {
    path: '/postDetail',
    name: 'postDetail',
    meta: { index: 1 },
    component: () => import('@/view/postDetail')
  },
  {
    path: '/replyMe',
    name: 'replyMe',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/replyMe'),
  },
  {
    path: '/historyComments',
    name: 'historyComments',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/historyComments')
  },
  {
    path: '/topPost',
    name: 'topPost',
    meta: { index: 1 },
    component: () => import('@/view/topPost'),
  },
  {
    path: '/historyPosts',
    name: 'historyPosts',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/historyPosts'),
  },
  {
    path: '/myCollect',
    name: 'myCollect',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/myCollect'),
  },
  {
    path: '/myConfig',
    name: 'myConfig',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/myConfig'),
  },
  {
    path: '/systemMessage',
    name: 'systemMessage',
    meta: { index: 1 },
    component: () => import('@/view/personalCenter/systemMessage'),
  },
  {
    path: '/main',
    name: 'layout',
    meta: { index: 1 },
    component: () => import('@/layout/layout'),
    children: [{
      path: 'index',
      name: 'index',
      meta: { index: 0, keepAlive: true },
      component: () => import('@/view/postsList')
    },
    {
      path: 'hot',
      name: '/hot',
      meta: { index: 0 },
      component: () => import('@/view/hot')
    },
    {
      path: 'evaluation',
      name: '/evaluation',
      meta: { index: 0 },
      component: () => import('@/view/evaluation')
    },
    ]
  }
  ],
  // scrollBehavior(to, from, savedPosition) { //解决拖动时多个页面互相影响的问题，当切换到新路由时，想要页面滚到顶部
  //   if (savedPosition) {
  //     console.log(savedPosition)
  //     return savedPosition
  //   } else {
  //       if (from.meta.keepAlive) {
  //         console.log('keepAlive')
  //         from.meta.savedPosition = document.body.scrollHeight
  //         console.log(from.meta.savedPosition)
  //       }
  //     }
  //     return {
  //         x: 0,
  //         y: 0
  //     }
  // }
})



