Vue.prototype.resetSetItem = function (key, newVal) {
  if (key === 'scrollToBottom') {

    // 创建一个StorageEvent事件
    var newStorageEvent = document.createEvent('StorageEvent');
    const storage = {
      setItem: function (k, val) {
        sessionStorage.setItem(k, val);

        // 初始化创建的事件
        newStorageEvent.initStorageEvent('setItem', false, false, k, null, val, null, null);

        // 派发对象
        window.dispatchEvent(newStorageEvent)
      }
    }
    return storage.setItem(key, newVal);
  }

}


Vue.prototype.showSnackbar = function (m, type = 'error', time = 1000) {
  store.change_snackbar({
    show: true,
    message: m.message || m,
    color: type == 'ok' ? "red lighten-2" : "grey darken-3",
    time: time
  })
}
Vue.prototype.$request = function (method, url, query, body) {
  let auth=Authorization.f()
  let host = this.$global.url
  let queryStr = `timeStamp=${new Date().getTime()}`
  for (let key in query) {
    queryStr += `&${key}=${query[key]}`

  }
  return new Promise((resolve, reject) => {
    axios({
      method,
      headers: {
        token: localStorage.getItem('token'),
        auth:auth
      },
      url: host + url + '?' + queryStr,
      data: body && JSON.stringify(body) != '{}' ? qs.stringify(body) : undefined,
      timeout: location.hash.includes('/login')?12000:6000
    })
      .then(res => {
        res = res.data
        if (res.status == 'error' && res.message == 'token错误,请重新登录') {
          Vue.prototype.showSnackbar(res.message)
          router.push('/login')
          reject(res)
        }
        if(res.status=='error'&&res.message=='黑名单token'){
          Vue.prototype.showSnackbar('token已经被加入黑名单')
          router.push('/login')
          reject(res)
        }
        if(res.status=='error'&&res.message=='黑名单token'){
          Vue.prototype.showSnackbar('token已经被加入黑名单')
          router.push('/login')
          reject(res)
        }
        if (res.status == 'ok')
          resolve(res)
        else reject(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}



