//store.js
import Vue from 'vue';

let store = {
  state: Vue.observable({
    tabBar: 0,
    toBottom: false,
    scrollY: 0,
    scrollYLeave: 0,
    keyboardShow: false,
    isUp: false,
    drawer: false,
    drawerName: '',
    // snackbar:{
    //   show:
    // }
    snackbar: {
      show: false,
      message: "",
      color: "",
      timeout: 1000
    },
    dialog:false,
    dialogName:''
  }),
  change_tab_bar(i) {
    console.log('changed')
    this.state.tabBar = i
  },
  change_to_bottom(message) {
    console.log('调用了')
    this.state.toBottom = message
  },
  change_scrollYLeave() {
    this.state.scrollYLeave = this.state.scrollY
  },
  change_scrollY(y) {
    this.state.scrollY = y
  },
  change_keyboard(message) {
    this.state.keyboardShow = message
  },
  change_isUp(message) {
    this.state.isUp = message
    console.log(message)
  },
  change_snackbar(snackbar) {
    this.state.snackbar = snackbar
  },
  change_drawer(v, drawerName) {
    if (drawerName == undefined)
      drawerName = this.state.drawerName
      
    this.state.drawer = v
    this.state.drawerName = drawerName
  },
  change_dialog(v,dialogName){
    if(!dialogName)dialogName=this.state.dialogName
    this.state.dialog=v
    this.state.dialogName=dialogName
  }
}
export default store



